.container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background: var(--bg, white);
  color: var(--text, black);
}

.card {
  width: 100%;
  max-width: 420px;
  background: var(--card-bg, #ffffff);
  border-radius: 8px;
  padding: 1.25rem;
  box-shadow: 0 6px 18px rgba(16,24,40,0.06);
}

.title {
  margin: 0 0 0.75rem 0;
  font-size: 1.25rem;
  font-weight: 600;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.label {
  font-size: 0.875rem;
}

.input {
  padding: 0.5rem 0.75rem;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  background: transparent;
}

.button {
  margin-top: 0.25rem;
  padding: 0.6rem 0.9rem;
  background: #2563eb;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.error {
  margin-top: 0.5rem;
  color: #dc2626;
  font-size: 0.9rem;
}
